define('charts/echarts.directive',[
    "./charts.module"
], function (app) {
    "use strict";
    app.directive("uiEcharts", echartsDirective);

    /* @ngInject */
    function echartsDirective(){
        var directive = {
            restrict: "AE",
            scope: true,
            controller: "EchartsController",
            controllerAs: "chart",
            bindToController: {
                title: "@",
                subTitle:"@?"
            }
        };
        return directive;
    }
});
