define('charts/echarts-series.directive',[
    "./charts.module",
    "./echarts.directive"
], function(app){
    "use strict";
    app.directive("uiEchartsSeries", echartsSeriesDirective);

    /* @ngInject */
    function echartsSeriesDirective(){
        var directive = {
            restrict: "AE",
            require: "^uiEcharts",
            controller: "EchartsSeriesController",
            controllerAs: "series",
            bindToController: {
                data: "<",
                name: "@",
                /**
                 * 
                 * 
                 * line
                 * map
                 * radar
                 * bar
                 * pie
                 * scatter
                 * ...
                 * @type String
                 */
                type: "@", // line
                /**
                 * 
                 */
                stack: "@?", 
                /**
                 * 
                 *  typeline
                 * @type Boolean
                 */
                smooth: "<",
                /**
                 * 
                 * 
                 * 'average' 
                 * 'max'
                 * 'min'
                 * 'sum'
                 */
                sampling: "@?",
                /**
                 * 
                 * {
                 *  color: "",
                 *  type: "solid",
                 *  width: 2,
                 *  opacity: 1
                 * }
                 */
                lineStyle: "<?"
            }
        };
        return directive;
    }
});
